<template>
    <div>
        <div class="row">
            <div class="col-md-12">
                <div class="card shadow rounded">
                    <div class="card-body mb-0">
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">N° de Expediente</label>
                                    <input type="text" class="form-control" placeholder="0001-2024-OAJ"
                                        v-model="datosgenerales.tra_number" disabled @input="validarCampo('tra_number')"
                                        :class="{ 'has-error': errores.tra_number }" />
                                    <span class="error-message" v-if="errores.tra_number">Ingrese un numero de
                                        expediente.</span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Fecha de llegada</label>
                                    <input class="form-control" type="date" v-model="datosgenerales.tra_arrival_date"
                                        @input="validarCampo('tra_arrival_date')"
                                        :class="{ 'has-error': errores.tra_arrival_date }" />
                                    <span class="error-message" v-if="errores.tra_arrival_date">Ingrese una fecha de
                                        llegada.</span>
                                </div>
                            </div>

                        </div>
                        <div class="row">

                        </div>

                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Oficina de origen</label>
                                    <select class="form-control" v-model="datosgenerales.tra_area"
                                        :class="{ 'has-error': errores.tra_area }" @change="validarCampo('tra_area')">
                                        <option value="" disabled selected class="py-5">-- Seleccione una opción
                                        </option>
                                        <option v-for="(area) in areas" :key="area.are_id" :value="area.are_id"
                                            class="py-3">
                                            {{ area.are_name }}
                                        </option>
                                    </select>
                                    <span class="error-message" v-if="errores.tra_area">Seleccione una
                                        Ocifina de origen.</span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">
                                        Responsable
                                    </label>

                                    <select class="form-control" v-model="datosgenerales.tra_abogado"
                                        :class="{ 'has-error': errores.tra_abogado }"
                                        @change="validarCampo('tra_abogado')">
                                        <option value="" disabled selected class="py-5">-- Seleccione una opción
                                        </option>
                                        <option v-for="(abogado, index) in abogados" :key="index"
                                            :value="abogado.abo_id" class="py-3">
                                            {{ abogado.nat_nombres }} {{ abogado.nat_apellido_paterno }} {{
                                                abogado.nat_apellido_materno }}
                                        </option>
                                    </select>
                                    <span class="error-message" v-if="errores.tra_abogado">Asigne
                                        un abogado</span>
                                </div>
                            </div>
                        </div>

                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Expediente de recepción (opcional)</label>
                                    <input type="text" class="form-control" v-model="datosgenerales.tra_exp_ext" />
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Documento de recepción (opcional)</label>
                                    <input type="text" class="form-control" v-model="datosgenerales.tra_doc_recep" />
                                </div>
                            </div>
                        </div>

                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Asunto</label>
                                    <textarea type="text" class="form-control" v-model="datosgenerales.tra_matter"
                                        @input="validarCampo('tra_matter')"
                                        :class="{ 'has-error': errores.tra_matter }"></textarea>
                                    <span class="error-message" v-if="errores.tra_matter">Ingrese un asunto de
                                        oficio</span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label">Información adicional</label>
                                    <textarea type="text" class="form-control"
                                        v-model="datosgenerales.tra_name"></textarea>
                                </div>
                            </div>
                        </div>

                        <div class="row py-3">

                            <div class="col-md-12 d-flex justify-content-end">
                                <button class="btn btn-primary btn-lg rounded" type="button" @click="actualizar">
                                    <i data-feather="edit"></i> Actualizar
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import feather from 'feather-icons';
import { getData, postData } from '@/utils/fetchData';
import { showLoadingAlert, closeLoadingAlert, warnignAlert, successAlert, errorAlert } from '@/utils/loader';
import Swal from 'sweetalert2';
export default {
    name: "Actualizar",
    props: {
        exp: {},
    },

    data() {
        return {
            trade: null,
            areas: [],
            abogados: [],
            //error si no se asigna responsables
            tra_person: false,
            datosgenerales: {
                tra_number: "",
                tra_name: "",
                tra_exp_ext: "",
                tra_doc_recep: "",
                //tra_are_id
                tra_area: "",
                tra_matter: "",
                tra_arrival_date: "",
                //Oficio en ejecucion
                tra_state_law: "P",
                //tra_abo_id
                tra_abogado: "",
                anio: new Date().getFullYear()
            },

            errores: {
                tra_number: false,
                tra_area: false,
                tra_matter: false,
                tra_arrival_date: false,
                tra_abogado: false,
            },
        };
    },

    created() {
        this.id = this.$route.params.id;
        this.cargarOficio();
        this.listarAreas();
        this.listarAbogados();
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },

    methods: {
        async cargarOficio() {
            this.$showLoadingAlert();
            try {
                const response = await this.$getData(`trade/${this.id}`, this);
                this.trade = response.trade;
                this.datosgenerales.tra_number = this.trade.tra_number;
                this.datosgenerales.tra_name = this.trade.tra_name;
                this.datosgenerales.tra_matter = this.trade.tra_matter;
                this.datosgenerales.tra_arrival_date = this.trade.tra_arrival_date;
                this.datosgenerales.tra_abogado = this.trade.tra_abo_id;
                this.datosgenerales.tra_area = this.trade.tra_are_id;
                this.datosgenerales.tra_type_ref = this.trade.tra_type_id;
                this.datosgenerales.tra_exp_ext = this.trade.tra_exp_ext;
                this.datosgenerales.tra_doc_recep = this.trade.tra_doc_recep;
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente administrativo ', this.id);
            }
        },

        async listarAreas() {
            try {
                const result = await getData('area', this);
                this.areas = result.data;
            } catch (error) {
                this.$handleApiError(error, 'listar areas');
            }
        },

        async listarAbogados() {
            try {
                const result = await getData('lawyer', this);
                this.abogados = result.data;
            } catch (error) {
                this.$handleApiError(error, 'listar abogados');
            } finally {
                this.$closeLoadingAlert();
            }
        },

        async actualizar() {
            if (this.validarCampos()) {
                const area = this.areas.find(area => area.are_id === this.datosgenerales.tra_area);
                try {
                    this.loader = true;
                    const formData = {
                        tra_id: this.id,
                        tra_name: this.datosgenerales.tra_name,
                        tra_exp_ext: this.datosgenerales.tra_exp_ext,
                        tra_doc_recep: this.datosgenerales.tra_doc_recep,
                        tra_matter: this.datosgenerales.tra_matter,
                        tra_arrival_date: this.datosgenerales.tra_arrival_date,
                        tra_ubication: area.are_name,
                        tra_type_id: this.datosgenerales.tra_type_ref,
                        tra_are_id: this.datosgenerales.tra_area,
                        tra_abo_id: this.datosgenerales.tra_abogado,
                    }
                    const response = await this.$putData('trade/update', this, formData);
                    if (response.state === 'success') {
                        Swal.fire({
                            title: 'Actualización Correcta',
                            text: 'Datos Actualizados Correctamente',
                            icon: 'success',
                            timer: 7000,
                            showConfirmButton: false // Evita que el usuario cierre la alerta manualmente
                        });
                        this.trade = response.data;
                        this.loader = false;
                        setTimeout(() => {
                            window.location.reload();
                        }, 1500);
                    }
                } catch (error) {
                    // Captura y maneja el error aquí
                    console.error('Error en la función actualizar:', error);

                    // Puedes mostrar mensajes de error o realizar otras acciones según tus necesidades
                    if (error.response) {
                        console.error('Respuesta del servidor:', error.response.data);
                    } else if (error.message) {
                        console.error('Mensaje de error:', error.message);
                    }

                    this.$handleApiError(error, 'crear oficio');
                }
            } else {
                console.log('error');
            }
        },

        validarCampo(campo) {
            this.errores[campo] = this.datosgenerales[campo] === "";
        },

        validarCampos() {
            Object.keys(this.errores).forEach(propiedadError => {
                this.validarCampo(propiedadError, propiedadError);
            });
            return !Object.values(this.errores).some(error => error);
        },

        goBack() {
            if (this.$store.getters.get__user.usu_rol === 'ABOGADO') {
                this.$router.push({ path: '/oficio/asignados' });
            } else {
                this.$router.go(-1);
            }
        }

    }
};
</script>

<style scoped>
.currency-symbol {
    position: absolute;
    padding: 10px 20px;
    background-color: transparent;
}

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    /* padding-left: 15px; */
    color: #EA4D4D;
    font-size: 12px;
}

@media only screen and (max-width: 600px) {
    .pdf-div {
        display: block !important;
    }

    .pdf-input {
        width: 100% !important;
        margin-top: 1em;
        margin-bottom: 1em;
    }

    .pdf-btn {
        width: 100%;
    }
}
</style>